<template>
  <div class="wrapperDialog">
    <div class="dialog">
      <div class="dialog__info">
        <br>
        <h3>Information</h3>
        <img class="dialog__info__image" :src="'../../../public/assets/' + image_url" />
        <div class="dialog__info_description">
          <p>Name: {{user.full_name}}</p>
          <p>Age: {{user.age}}</p>
          <p>Sex: {{user.sex}}</p>
          <p>Information: {{user.information}}</p>
          <p>Fb: {{user.link_fb}}</p>
          <p>Phone: {{user.tel}}</p>
          <button class="dialog__cancel btn">Edit</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import axios from 'axios'
import authHeader from '../../services/auth-header'

export default {
 data() {
   return {
    image_url: JSON.parse(localStorage.getItem("token")).user.image_name,
    user: JSON.parse(localStorage.getItem("token")).user
   }
 },
 created() {
  axios
  .get(`http:localhost:3000/users/${JSON.parse(localStorage.getItem("token")).user.id}`, { headers: authHeader() })
  .then(response => {
    this.image_url = response.data.image_url
  })
 },
 computed: {
   ...mapGetters(['accept', 'confirm', 'account'])
 },
 methods: {
   ...mapActions(['toggleAccept', 'toggleDialog', 'connection'])
 }
}
</script>

<style lang="scss" scoped>
  .wrapperDialog {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    z-index: 1001;
    .dialog {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 1000;
      transform: translate(-50%, -50%);
      background: #FFF;
      padding: 20px;
      &__title {
        font-size: 20px;
        text-align: center;
        margin-bottom: 20px;
      }
      .btn {
        padding: 10px 20px;
        outline: none;
        border-radius: 10px;
        color: #FFF;
      }
      &__info {
        h3 {
          text-align: center;
        }
        &__image {
          width: 300px;
        }
      }
      &__connection {
        background: rgb(32, 189, 1);
        transition: all .3s ease;
        border: 1px solid  rgb(32, 189, 1);
        &:hover {
          background: #FFF;
          color: rgb(32, 189, 1);
        }
      }
      &__cancel {
        background: rgb(247, 58, 58);
        transition: all .3s ease;
        border: 1px solid  rgb(247, 58, 58);
        &:hover {
          background: #FFF;
          color: rgb(247, 58, 58);
        }
      }
    }
  }
</style>



