<template>
  <div class="vue-tempalte">
    ahshdahshd
  </div>
</template>

<script>
import axios from "axios"
import { mapActions } from 'vuex'
import authHeader from '../../services/auth-header'

export default {
  name: "home",
  data() {
    return {
      products: [],
      carts: [],
      errors: []
    };
  },
  created() {
    this.cream();
  },
  methods: {
    ...mapActions(['updateCart'])
  }
};
</script>

<style scoped lang="scss">
  @import '../../scss/mixin.scss';
  .vue-tempalte {
    align-items: center;
    .item-cart {
      align-items: center;
      width: 946px;
      height: 205px;
      left: 247px;
      top: 258px;

      background: #FFFFFF;
      border: 0.5px solid #FF9595;
      box-sizing: border-box;
      border-radius: 5px;
      margin-bottom: 30px;
      .product__image {
        width: 149px;
        height: 155px;
        left: 262px;
        top: 288px;
      }
      .box {
        .product__name {
          @include customFont(20px, 'roboto');
          text-transform: capitalize;
          text-align: center;
        }
        .product__point {
          @include customFont(18px, 'roboto');
          text-align: center;
          margin: auto;
          .product__point__icon {
            height: 18px;
          }
        }
        product__pay {
          text-align: center;
          @include customFont(18px, 'roboto');
          color: $colorOrange;
        }
      }
    }
  }
</style>



